.contacts__tel {
    display: block;
    margin-bottom: 15px;
}

.contacts__text {
    margin: 0;
    margin-bottom: 20px;
    @include normal;
    font-size: 14px;
    line-height: 18px;
    color: #000000;
}

.contacts__mail {
    @include normal;
    font-size: 16px;
    line-height: 16px;
    color: $color-font-yellow;

    &:hover,
    &:focus {
        color: #E3AB00;
    }
}